body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: darkcyan;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



button{
  text-align: center;
    padding: 17px 40px;
    border-radius: 15px;
    font-size: 1rem;
    line-height: 1rem;
    box-sizing: border-box;
    border: 2px solid #fe7333;
    /* font-weight: 700; */
    white-space: nowrap;
    background-color: #fe7333;
    color: #fff;
    cursor:pointer;
}


button:disabled{
  cursor:default;
  filter: grayscale(100%);
  pointer-events: none;
}


button:hover{
  background-color: #fe8f5c;
  color: #fff;
  border: 2px solid #fe8f5c;
}

button:focus{
  background-color: #fe7333;
  border: 2px solid #e24700;
}

button:active{
  background-color: #ff5000;
  border: 2px solid #ff5000;
}

input{
    box-sizing: border-box;
    width: 100%;
    padding: 13px 20px;
    font-size: 1rem;
    height: 54px;
    outline: none;
    color: #000;
    cursor: pointer;
    white-space: nowrap;
    text-overflow: ellipsis;
    border:none;
    background-color: #fff;
}


input:active{
  color: #292929;
}

input:disabled{
  cursor:default;
  filter: grayscale(100%);
  pointer-events: none;
  opacity: .5;
}