

.listItem{
    position: relative;
    padding: 15px;
    overflow: hidden; 
    text-overflow: ellipsis; 
    white-space: nowrap;
    cursor: pointer;
}





.listItem:not(:last-child){
    margin-bottom: 10px;
    border-bottom: 1px solid #ccc;
}

.listItem:hover{
    border-color: grey;
}


