

.containerOuther{
    width: calc(100vw - 82px);
    height: calc(100vh - 82px);
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-template-rows: 1fr auto;
    grid-template-areas: "list note"
                         "total controls";
    gap: 10px;
    padding: 20px;
    margin: 20px;
    border-radius: 10px;
    background-color: #ffffff;
}


.blockControls{
    grid-area: controls;
    /* background-color:beige; */
    
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px
}

.totalInfo{
    grid-area: total;
    display: flex;
    align-items: center;
    color: #686868;
}

.listItems{
    position: relative;
    grid-area: list; 
    overflow: auto;
    border-bottom: 1px solid #c3c3c3;;
}

.listItems > span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    opacity: .5;
}

.listNotes{
    overflow-y: auto;
}

.blockTotal{
    margin-bottom: 10px;
    color:#c3c3c3
}

.listItems > ul{
    margin: 0;
    padding: 0;
    list-style: none;
}

.editBlock{
    /* padding: 10px; */
    /* display: flex;
    flex-direction: column; */
    grid-area: note;
    /* background-color: beige; */
}




.blockTextArea{
    box-sizing: border-box;
    padding: 20px 10px;
    width: 100%;
    height: 100%;
    resize: none;
    background-color: #fff;
    border: 1px solid #c3c3c3;
    border-radius: 15px;
    font-size: 1rem;
    color: #000;
    outline: none !important;
}

.blockTextArea:focus{
    border-color: #686868;
}


@media(max-width:767px){
    .containerOuther{
        padding: 10px;
        padding-top: 55px;
        margin: 10px;
        /* grid-template-columns: auto;
        grid-template-rows: repeat(3, max-content);
        grid-template-areas: "controls"
                             "note"
                             "total"
                             "list"; */

        display: flex;
        flex-direction: column;
        min-height: 100vh;
        height: auto;
        width: auto;
    }

    .listItems{
        order: 1;
        flex-grow: 1;
        overflow: initial;
    }

    .blockControls{
        flex-direction: column-reverse;
        align-items: stretch;
        justify-content: flex-start;
        /* min-height: 100vh; */
    }

    .blockTextArea{
        min-height: 250px;
    }

    .listItems{
        border-bottom: none;
    }

    .listItems > span{
        position: static;
        font-size: 14px;
    }
}