



.containerInner{
    position: relative;
    width: 100%;
    border-bottom: 1px solid #ccc;
}



.containerInner > input{
    padding-left: 50px;
}

.searchImg{
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    width: 25px;
    height: 25px;
}


@media(max-width:767px) {
    .containerInner{
        position: fixed;
        top: 0;
        left: 10px;
        right: 10px;
        width: auto;
        z-index: 100;
        background-color: #fff;
    }

  

    
}